
.box {
  width: 100%;
}

.area {
  width: 100%;
  max-width: 1420px;
  display: flex;
  margin: 0 auto;
  padding: 32px;
  justify-content: space-between;
  align-items: center;
}

.info {
  display: flex;
  margin: 0;
  align-items: center;
}

.titles {
  margin-left: 32px;
  gap: 4px;
}

.title {
  text-transform: uppercase;
  margin: 0;
  font-size: 1rem;
  line-height: 1.4;
  font-weight: 700;
}

.subtitle {
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
}

.contact {
  padding: 6px 13px;
  background-color: rgb(255, 243, 230);
  display: flex;
  flex-direction: column;
  position: relative;

  &:before {
    content: '16+';
    color: #0c79be;
    position: absolute;
    border: 2px solid #0c79be;
    border-radius: 50%;
    right: 0;
    bottom: calc(100% + 10px);
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  a {
    padding-bottom: 0.05em;
    transition: border-color .15s ease-in;
    color: inherit;
    text-decoration: none;
    font-size: 20px;
    font-weight: 600;
  }

  span {
    font-size: 14px;
    line-height: 22px;
  }
}
