.box{display:flex;justify-content:space-between;align-items:center}.text{font-size:17px}.switch{width:100%;max-width:140px;height:100%;border:2px solid #0c79be;border-radius:5px;position:relative;cursor:pointer}.switch:before{display:flex;justify-content:center;align-items:center;content:"Нет";position:absolute;left:0;width:50%;height:100%;background:#0c79be;color:#fff;transition:left .4s;border-radius:3px}.switch.true:before{left:50%;content:"Да"}