
.box {
  width: 100%;
  background-color: rgb(12, 121, 190);
  color: white;
  padding: 32px 0;
}

.area {
  width: 100%;
  max-width: 1420px;
  display: flex;
  margin: 0 auto;
  padding: 32px 0;
  justify-content: space-between;

  &:first-child {
    border-top: 1px solid white;
  }
}

.info {
  margin: 0;
  max-width: 316px;
}

.text {
  font-size: 14px;
  font-weight: 500;
}

.about {
  display: flex;

  a {
    display: flex;
    flex-shrink: 0;
    width: 74px;
    margin-right: 8px;
  }

  p {
    font-size: 12px;
    font-weight: 700;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  position: relative;
}

.block {

  display: flex;
  flex-direction: column;
  margin-bottom: 32px;

  span {
    font-size: 14px;
    line-height: 22px;
    margin-bottom: 10px;
  }

  a, p {
    margin: 0;
    transition: border-color .15s ease-in;
    color: inherit;
    text-decoration: none;
    font-size: 20px;
    font-weight: 600;
  }
}

.links {
  display: flex;
}

.link {
  display: flex;
  width: 48px;
  height: 48px;
  margin-right: 20px;

  svg {
    fill: white;
  }
}
