
.box {
  width: 100%;
}

.area {
  width: 100%;
  max-width: 1420px;
  display: flex;
  margin: 0 auto;
  padding: 32px 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  font-size: 34px;
  margin-bottom: 30px;
}

.form {
  width: 100%;
  max-width: 400px;
  margin: 0 auto 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  border: 1px solid #EEEEEE;
  border-radius: 5px;
  color: #303030;
  font-size: 17px;
  font-weight: 400;
  height: 30px;
  padding: 9px 12px;
  width: 100%;
  margin-bottom: 20px;
}

.submit {
  border: 2px solid rgb(12, 121, 190);
  border-radius: 5px;
  background: transparent;
  height: 40px;
  padding: 9px 12px;
  width: 100%;
  max-width: 300px;
  cursor: pointer;

  &:hover {
    background: rgb(12, 121, 190);
    color: white;
  }

  &:disabled {
    background: rgb(12, 121, 190);
    color: white;
    cursor: not-allowed;
  }
}


